import { Box, Typography } from '@material-ui/core'
import CenterBox from 'component/center-box'
import { Icon, IconProps } from 'component/icons'
import React from 'react'
import CardContainer from './card-container'
import { useStyles } from './story-card.style'

export type StoryCardProps = {
	iconImg: IconProps['imgPath']
	title: string
	desc: string
	mainImg: string
	bottomImg: string
}
const StoryCard = (p: StoryCardProps) => {
	const styles = useStyles()
	return (
		<CardContainer>
			<Box display='flex' flexDirection='column' alignItems='center'>
				<CenterBox mt={2} mb={2} className={styles.iconContainer}>
					<Icon imgPath={p.iconImg} />
				</CenterBox>
				<Typography align='center' variant='subtitle2'>
					{p.title}
				</Typography>
			</Box>
			<Box width='100%' display='flex' alignItems='center' flexDirection='column'>
				<Box ml={4} mr={4}>
					<img className={styles.mainImg} src={p.mainImg} alt='a story' />
					<Box mt={1} mb={2}>
						<Typography variant='caption'>{p.desc}</Typography>
					</Box>
				</Box>

				<Box className={styles.storyCardBottom}>
					<img src={p.bottomImg} alt='background' />
				</Box>
			</Box>
		</CardContainer>
	)
}
export default StoryCard
