import React from 'react'
import { StyleProps, useStyles } from './index.style'
import Particles from 'react-tsparticles'
import particlesConf from 'component/particles/particles-config-snow.json'

export type BgContainerProps = {
	header: JSX.Element | JSX.Element[]
	main: JSX.Element | JSX.Element[]
} & { styleOptions?: StyleProps }

const BgContainer = (p: BgContainerProps) => {
	const styles = useStyles(p.styleOptions)
	return (
		<div className={styles.container}>
			<div className={styles.bg} />
			<div className={styles.particlesContainer}>
				<Particles id='tsparticles' className={styles.particlesContainer} options={particlesConf} />
			</div>
			<header className={`${styles.header} ${styles.dynamicHeader}`}>{p.header}</header>
			<main className={styles.main}>{p.main}</main>
		</div>
	)
}
export default BgContainer
