import { makeStyles } from '@material-ui/core'
import { Images } from 'utils/cos-resource'

const url = (img: string) => `url(${img})`
const bgImg = Images.bg

const cardHeight = '16vh'
const cardWidth = '38vw'

type CardStyleProps = {
	revealed: boolean
	frontSideImg: string
}

const card = {
	height: cardHeight,
	width: cardWidth,
	maxWidth: '200px',
	borderRadius: '10px',
	transition: 'all 0.2s ease-in',
	backgroundSize: 'contain',
	backgroundRepeat: 'no-repeat',
	backgroundPosition: 'center',
	backgroundColor: '#fce3e3',
	padding: '0 3px',
	display: 'flex',
	alignItems: 'center',
	border: '2px solid white'
}

const cardRevealed = {
	...card,
	transform: 'rotateY(180deg)',
	backgroundSize: 'cover',
	backgroundImage: url(bgImg)
}

const image = {
	backgroundImage: (p: CardStyleProps) => (p.revealed ? url(p.frontSideImg) : 'none'),
	backgroundSize: 'contain',
	backgroundPosition: 'center',
	backgroundRepeat: 'no-repeat',
	height: '80%',
	width: '100%'
}

export const useStyles = makeStyles({
	card,
	cardRevealed,
	image
})
