import { Box, Typography } from '@material-ui/core'
import { GAME_PAGE } from 'App'
import BgContainer from 'component/bg-container'
import Button from 'component/button'
import React from 'react'
import { useHistory } from 'react-router-dom'
import { Images } from 'utils/cos-resource'
import GameTitle from 'view/game-title'
import { useStyles } from './index.style'

export type LandingPageProps = {}
const LandingPage = (p: LandingPageProps) => {
	const styles = useStyles()
	const history = useHistory()
	/* -------------------------------------------------------------------------- */
	/*                                event handler                               */
	/* -------------------------------------------------------------------------- */
	const onStartGameButtonClick = () => {
		history.push(GAME_PAGE)
	}
	return (
		<BgContainer
			header={
				<>
					<GameTitle />
					<Box mb='23px'>
						<Typography variant='body2' align='center' color='textSecondary'>
							翻开卡牌，寻找故事碎片
						</Typography>
					</Box>
				</>
			}
			main={
				<Box width='100%'>
					<section>
						<Box display='flex' justifyContent='center'>
							<img className={styles.card} src={Images.cardBackBig} alt='card-background' />
						</Box>
					</section>
					<Box margin={6} />
					<section>
						<Box display='flex' justifyContent='center'>
							<Button variant='contained' onClick={onStartGameButtonClick}>
								<Typography className={styles.buttonFont} variant='body2' align='center' color='textPrimary'>
									开始游戏
								</Typography>
							</Button>
						</Box>
					</section>
				</Box>
			}
		/>
	)
}
export default LandingPage
